import {ACCOUNT_FORM, SET_CLIENT_TYPE} from "../_types";
import GetCompanyForm from "../pages/AddOrder/Forms/GetCompanyForm";
import CompanyGeneralDataForm from "../pages/AddOrder/Forms/CompanyGeneralDataForm";
import CompanyDocumentsTable from "../pages/AddOrder/Forms/CompanyDocumentsTable";
import AddressContactTab from "../pages/AddOrder/Forms/AddressContactsTab";
import FoundersTable from "../pages/AddOrder/Forms/FoundersTable";
import AuthorizedPersonsTable from "../pages/AddOrder/Forms/AuthorizedPersonsTable";
import AccountsForm from "../pages/AddOrder/Forms/AccountsForm";
import SendDocumentsForm from "../pages/AddOrder/Forms/SendDocumentsForm";
import CompanyIpGeneralDataForm from "../pages/AddOrder/Forms/CompanyIpGeneralDataForm";

export const companyUrl = '/dashboard/add/company/company';
export const baseUrl = '/dashboard/add/company/base';
export const documentsUrl = '/dashboard/add/company/documents';
export const contactsUrl = '/dashboard/add/company/contacts';
export const foundersUrl = '/dashboard/add/company/founders';
export const authorizedPersonsUrl = '/dashboard/add/company/authorized-persons';
export const accountsUrl = '/dashboard/add/company/accounts';
export const attachmentsUrl = '/dashboard/add/company/attachments';

const namespace = ACCOUNT_FORM;

export const addCompanyTabs = [
  {url: companyUrl,   label: '', component: GetCompanyForm, acceptFor: ["", "", ""]},
  {url: baseUrl,  label: ' ', component: CompanyIpGeneralDataForm, acceptFor: ["", ""]},
  {url: baseUrl,  label: ' ', component: CompanyGeneralDataForm, acceptFor: [""]},
  {url: documentsUrl,  label: '', header: ' ', component: CompanyDocumentsTable, acceptFor: [""]},
  {url: contactsUrl,  label: '  ', component: AddressContactTab, acceptFor: ["", ""]},
  {url: foundersUrl,  label: '', component: FoundersTable, acceptFor: [""]},
  {url: authorizedPersonsUrl,  label: ' ', component: AuthorizedPersonsTable, acceptFor: ["", ""]},
  // {url: '/dashboard/add/company/products',    label: '', component: ProductsForm},
  {url: accountsUrl, label: ' ', component: AccountsForm, acceptFor: ["", ""]},
  {url: attachmentsUrl, label: '', component: SendDocumentsForm, acceptFor: ["", "", ""], access: true},
];

const initState = {
  clientType: "",
};

export default function accountForm(state = initState, {type, payload}) {
  // CHECK NAMESPACE
  if (type.indexOf(namespace)!==0) return state;
  type = type.replace(namespace, '');
  
  if (type === SET_CLIENT_TYPE) {
    state.clientType = payload;
  }
  
  return {...state};
}
